'use client';

import { useState } from 'react';
import TaskItem from './task-item';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useLanguage } from '@/contexts/language-context';

const allTasks = [
  { id: 1, title: 'Check Soil Moisture', description: 'Ensure soil is moist but not waterlogged.', points: 10, category: 'Soil Health', crop: 'Paddy' },
  { id: 2, title: 'Apply Organic Fertilizer', description: 'Use compost or manure to enrich the soil.', points: 25, category: 'Fertilization', crop: 'Paddy' },
  { id: 3, title: 'Scout for Pests', description: 'Look for common pests like stem borers.', points: 15, category: 'Pest Control', crop: 'Paddy' },
  { id: 4, title: 'Repair Irrigation Drip Lines', description: 'Check for leaks and clogs to conserve water.', points: 20, category: 'Water Management', crop: 'Vegetables' },
  { id: 5, title: 'Add Mulch Layer', description: 'Apply a layer of straw or leaves to retain moisture.', points: 15, category: 'Soil Health', crop: 'Vegetables' },
  { id: 6, title: 'Prune Lower Leaves', description: 'Improve air circulation to prevent fungal diseases.', points: 10, category: 'Plant Health', crop: 'Vegetables' },
  { id: 7, title: 'Prepare Land for Planting', description: 'Plow and level the field for the upcoming season.', points: 30, category: 'Preparation', crop: 'Wheat' },
  { id: 8, title: 'Test Soil pH', description: 'Check if the soil pH is optimal for wheat growth (6.0-7.0).', points: 20, category: 'Soil Health', crop: 'Wheat' },
];

const crops = ['Paddy', 'Vegetables', 'Wheat'];
const cropTranslations: { [key: string]: { [lang: string]: string } } = {
  Paddy: { en: 'Paddy', ml: 'നെല്ല്' },
  Vegetables: { en: 'Vegetables', ml: 'പച്ചക്കറികൾ' },
  Wheat: { en: 'Wheat', ml: 'ഗോതമ്പ്' },
};


export default function TaskList() {
  const [selectedCrop, setSelectedCrop] = useState('Paddy');
  const { language, t } = useLanguage();
  const filteredTasks = allTasks.filter(task => task.crop === selectedCrop);

  return (
    <Card>
      <CardHeader>
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
          <CardTitle>{t('todaysTasks', 'dashboard')}</CardTitle>
          <div className="flex items-center gap-2">
            <span className="text-sm font-medium">{t('crop', 'dashboard')}:</span>
            <Select value={selectedCrop} onValueChange={setSelectedCrop}>
              <SelectTrigger className="w-[180px]">
                <SelectValue placeholder={t('selectCrop', 'dashboard')} />
              </SelectTrigger>
              <SelectContent>
                {crops.map(crop => (
                  <SelectItem key={crop} value={crop}>
                    {cropTranslations[crop][language]}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        {filteredTasks.length > 0 ? (
          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
            {filteredTasks.map(task => (
              <TaskItem key={task.id} task={task} />
            ))}
          </div>
        ) : (
          <p className="text-center text-muted-foreground py-8">{t('noTasks', 'dashboard')}</p>
        )}
      </CardContent>
    </Card>
  );
}
