'use server';

import {
  verifyLandDocAuthenticity,
  VerifyLandDocAuthenticityInput,
} from '@/ai/flows/verify-land-doc-authenticity';
import {
  validateFarmCropContext,
  ValidateFarmCropContextInput,
} from '@/ai/flows/validate-farm-crop-context';

export async function verifyDocumentAction(
  input: VerifyLandDocAuthenticityInput
) {
  return await verifyLandDocAuthenticity(input);
}

export async function validateTaskAction(input: ValidateFarmCropContextInput) {
  return await validateFarmCropContext(input);
}
